<template>
  <div class="modal modal-sm" :class="{active: active}">
  <a class="modal-overlay" @click="close" ></a>
  <div class="modal-container">
    <div class="modal-header">
      <a class="btn btn-clear float-right" @click="close"></a>
      <!-- <div class="modal-title h5">About</div> -->
    </div>
    <div class="modal-body">
      <div class="content">
        <!-- content here -->
        <div class="about-image">
          <img src="/static/img/bg.jpg" alt="">
        </div>

        <div class="about-text">

        </div>

      </div>
    </div>
    <div class="modal-footer">
      <!-- ... -->
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'About',
  props: {
    active: true
  },
  methods: {
    close () {
      this.$emit('close')
    }
  }
}
</script>

<style scoped>

.about-image img {

}

</style>



